import React from 'react';
import PropTypes from 'prop-types';
import { View } from '@vkontakte/vkui';

import Home from '../panels/Home';
import CitySelector from '../panels/CitySelector';
import SpecialitySelector from '../../containers/panels/SpecialitySelector';

const Search = ({ activePanel, toHome }) => (
  <View activePanel={activePanel}>
    <Home id="Home" />
    <CitySelector id="CitySelector" onBack={toHome} />
    <SpecialitySelector id="SpecialitySelector" onBack={toHome} />
  </View>
);

Search.propTypes = {
  activePanel: PropTypes.oneOf(['Home', 'CitySelector', 'SpecialitySelector'])
    .isRequired,
  toHome: PropTypes.func.isRequired
};

export default Search;
