import actions from '../actions';

export const view = (state = { activePanel: 'Home' }, action) => {
  switch (action.type) {
    case actions.SHOW_CITY_SELECTOR:
      return {
        ...state,
        activePanel: 'CitySelector'
      };
    case actions.SHOW_HOME:
      return {
        ...state,
        activePanel: 'Home'
      };
    case actions.SHOW_SPECIALITY_SELECTOR:
      return {
        ...state,
        activePanel: 'SpecialitySelector'
      };
    case '@@redux-form/CHANGE':
      return {
        ...state,
        activePanel: 'Home'
      };
    default:
      return state;
  }
};

export default (state = { page: 0, results: [], isLoading: false }, action) => {
  switch (action.type) {
    case actions.SEARCH:
      return {
        ...state,
        isLoading: true
      };
    case actions.SEARCH_SUCCESS:
      return {
        ...state,
        isLoading: false,
        page: action.payload.page,
        results:
          action.payload.page === 0
            ? action.payload.results
            : [...state.results, action.payload.results]
      };
    case actions.SEARCH_FAILURE:
      return {
        ...state,
        isLoading: false
      };
    default:
      return state;
  }
};
