import React from 'react';
import PropTypes from 'prop-types';
import { Panel, PanelHeader, HeaderButton, Spinner } from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import SpecialityList from '../../containers/organisms/SpecialityList';
import './SpecialitySelector.css';

class SpecialitySelector extends React.Component {
  componentWillMount() {
    const { city, load } = this.props;
    load(city);
  }

  render() {
    const { id, onBack, isLoading } = this.props;
    return (
      <Panel id={id} className="SpecialitySelector">
        <PanelHeader
          left={
            <HeaderButton onClick={onBack}>
              <Icon28ChevronBack />
            </HeaderButton>
          }
        >
          Выбор специальности
        </PanelHeader>
        {isLoading ? <Spinner size="large" /> : <SpecialityList />}
      </Panel>
    );
  }
}

SpecialitySelector.propTypes = {
  id: PropTypes.string.isRequired,
  onBack: PropTypes.func.isRequired,
  city: PropTypes.string.isRequired,
  isLoading: PropTypes.bool.isRequired,
  load: PropTypes.func.isRequired
};

export default SpecialitySelector;
