import { call, put } from 'redux-saga/effects';
import axios from 'axios';

import actions from '../actions';

export default function* loadCities() {
  const {
    data: { CityList }
  } = yield call(axios, { url: '/city' });
  yield put({
    type: actions.LOAD_CITIES_SUCCESS,
    payload: CityList
  });
}
