import { connect } from 'react-redux';

import SearchForm from '../../components/organisms/SearchForm';
import actions from '../../actions';

const mapDispatchToProps = dispatch => ({
  onSubmit: query =>
    dispatch({
      type: actions.SEARCH,
      payload: {
        city: query.city.Id,
        speciality: query.speciality.Id,
        when: query.date,
        page: 0
      }
    }),
  selectCity: () => dispatch({ type: actions.SHOW_CITY_SELECTOR }),
  selectSpeciality: () => dispatch({ type: actions.SHOW_SPECIALITY_SELECTOR })
});

export default connect(
  null,
  mapDispatchToProps
)(SearchForm);
