import { takeLatest, call, put } from 'redux-saga/effects';
import axios from 'axios';

import actions from '../actions';

function composeUrl({ city }) {
  return `/speciality/city/${city}`;
}

export function* loadSpecialities({ payload }) {
  try {
    const {
      data: { SpecList }
    } = yield call(axios, { url: composeUrl(payload) });
    yield put({
      type: actions.LOAD_SPECIALITIES_SUCCESS,
      payload: {
        city: payload.city,
        result: SpecList
      }
    });
  } catch (error) {
    yield put({
      type: actions.LOAD_SPECIALITIES_FAILURE,
      payload: {
        error
      }
    });
  }
}

export function* watchLoadSpecialities() {
  yield takeLatest(actions.LOAD_SPECIALITIES, loadSpecialities);
}
