import { connect } from 'react-redux';
import actions from '../../actions';

import SpecialitySelector from '../../components/panels/SpecialitySelector';

const mapStateToProps = (state, ownProps) => ({
  ...ownProps,
  isLoading: state.entities.specialities.isLoading,
  city: state.form.search.values.city.Id
});

const mapDispatchToProps = dispatch => ({
  load: city =>
    dispatch({
      type: actions.LOAD_SPECIALITIES,
      payload: { city }
    })
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(SpecialitySelector);
