import actions from '../actions';

export default (state = { isLoading: false, list: [] }, action) => {
  switch (action.type) {
    case actions.LOAD_SPECIALITIES:
      return {
        ...state,
        isLoading: true
      };
    case actions.LOAD_SPECIALITIES_SUCCESS:
      return {
        isLoading: false,
        list: action.payload.result
      };
    case actions.LOAD_SPECIALITIES_FAILURE:
      return {
        ...state,
        isLoading: false
      };
    default:
      return state;
  }
};
